import zTable from '../../components/z-table/z-table.vue';

export default {
	data() {
		return {
			title: 'zTable使用示例',
			baseTableData: [
				{
					name: '张三',
					age: 18,
					gender: '男'
				},
				{
					name: '赵四',
					age: 16,
					gender: '女'
				},
				{
					name: '王五',
					age: 20,
					gender: '男'
				},
				{
					name: '李六',
					age: 18,
					gender: '女'
				}
			],
			baseColumns: [
				{
					title: '姓名',
					key: 'name',
					width: 100
				},
				{
					title: '性别',
					key: 'gender',
					width: 400
				},
				{
					title: '年龄',
					key: 'age',
					width: 400
				}
			],
			baseHtml: '<z-table :tableData="baseTableData" :columns="baseColumns"></z-table>',
			// 固定左侧
			stickSideTableData: [{
				name: '张三',
				age: 18,
				gender: '男'
			},{
				name: '赵四',
				age: 16,
				gender: '女'
			},{
				name: '王五',
				age: 20,
				gender: '男'
			},{
				name: '李六',
				age: 18,
				gender: '女'
			}],
			stickSideColumns: [{
				title: '姓名',
				key: 'name',
				width: 100
			},{
				title: '性别',
				key: 'gender',
				width: 400
			},{
				title: '年龄',
				key: 'age',
				width: 400
			}],
			stickSideHtml: '<z-table :tableData="stickSideTableData" :columns="stickSideColumns" stickSide="true"></z-table>',
			// 底部统计表格
			showBottomSumTableData: [{
				name: '张三',
				age: 18,
				gender: '男'
			},{
				name: '赵四',
				age: 16,
				gender: '女'
			},{
				name: '王五',
				age: 20,
				gender: '男'
			},{
				name: '李六',
				age: 18,
				gender: '女'
			}],
			showBottomSumColumns: [{
				title: '姓名',
				key: 'name',
				width: 100
			},{
				title: '性别',
				key: 'gender',
				width: 400
			},{
				title: '年龄',
				key: 'age',
				width: 400
			}],
			showBottomSumHtml: '<z-table :tableData="showBottomSumTableData" :columns="showBottomSumColumns" showBottomSum="true"></z-table>',
			// 自定义内容的表格
			customTableData: [{
				name: '张三',
				age: 18,
				gender: '男'
			},{
				name: '赵四',
				age: 16,
				gender: '女'
			},{
				name: '王五',
				age: 20,
				gender: '男'
			},{
				name: '李六',
				age: 18,
				gender: '女'
			}],
			customColumns: [{
				title: '姓名',
				format: {
				    template: "我叫 #name#",
				    names: ["name"]
				},
				width: 200
			},{
				title: '性别',
				key: 'gender',
				width: 400
			},{
				title: '年龄',
				key: 'age',
				width: 400
			}],
			customHtml: '<z-table :tableData="customTableData" :columns="customColumns"></z-table>',
			// 单元格为链接
			isLinkTableData: [
				{
					name: '张三',
					age: 18,
					gender: '男'
				},
				{
					name: '赵四',
					age: 16,
					gender: '女'
				},
				{
					name: '王五',
					age: 20,
					gender: '男'
				},
				{
					name: '李六',
					age: 18,
					gender: '女'
				}
			],
			isLinkColumns: [
				{
					title: '姓名',
					key: 'name',
					isLink: {
						url: "https://www.baidu.com",
						params: ["from|name"]
					},
					width: 100
				},
				{
					title: '性别',
					key: 'gender',
					width: 400
				},
				{
					title: '年龄',
					key: 'age',
					width: 400
				}
			],
			isLinkHtml: '<z-table :tableData="isLinkTableData" :columns="isLinkColumns"></z-table>',
			// 空字符串
			emptyTableData: [],
			emptyColumns: [
				{
					title: '姓名',
					key: 'name',
					empty: {
						url: "https://www.baidu.com",
						params: ["from|name"]
					},
					width: 100
				},
				{
					title: '性别',
					key: 'gender',
					width: 400
				},
				{
					title: '年龄',
					key: 'age',
					width: 400
				}
			],
			emptyHtml: '<z-table :tableData="emptyTableData" :columns="emptyColumns" :showLoading="false" emptyText="设置了showLoading=false才会看到我"></z-table>',
			// 自定义高度
			heightTableData: [
				{
					name: '张三',
					age: 18,
					gender: '男'
				},
				{
					name: '赵四',
					age: 16,
					gender: '女'
				},
				{
					name: '王五',
					age: 20,
					gender: '男'
				},
				{
					name: '李六',
					age: 18,
					gender: '女'
				}
			],
			heightColumns: [
				{
					title: '姓名',
					key: 'name',
					height: {
						url: "https://www.baidu.com",
						params: ["from|name"]
					},
					width: 100
				},
				{
					title: '性别',
					key: 'gender',
					width: 400
				},
				{
					title: '年龄',
					key: 'age',
					width: 400
				}
			],
			heightHtml: '<z-table :tableData="heightTableData" :columns="heightColumns" :tableHeight="130"></z-table>',
			// 开启排序
			sortTableData: [
				{
					name: '张三',
					age: 18,
					gender: '男'
				},
				{
					name: '赵四',
					age: 16,
					gender: '女'
				},
				{
					name: '王五',
					age: 20,
					gender: '男'
				},
				{
					name: '李六',
					age: 18,
					gender: '女'
				}
			],
			sortColumns: [
				{
					title: '姓名',
					key: 'name',
					width: 100
				},
				{
					title: '性别',
					key: 'gender',
					sort: true,
					width: 400
				},
				{
					title: '年龄',
					key: 'age',
					sort: true,
					width: 400
				}
			],
			sortHtml: '<z-table :tableData="sortTableData" :columns="sortColumns" @sort="doSort"></z-table>',
		};
	},
	components: {
		zTable
	},
	methods: {
		doSort(res) {
			uni.showToast({
				title: `点击了${res.key}的排序, 排序方式为${res.type}`,
				icon: 'none'
			})
		}
	}
};
